import React, { useState, useEffect } from 'react';
import FilterPerson from './components/FilterPerson';
import AddPerson from './components/AddPerson';
import Person from './components/Person';
import peopleService from './services/people';

const App = () => {
  const initialState = '';
  const [people, setPeople] = useState([]);
  const [newName, setNewName] = useState(initialState);
  const [newNumber, setNewNumber] = useState(initialState);
  const [newQuery, setNewQuery] = useState(initialState);
  const [errorMessage, setErrorMessage] = useState(null);
  const [message, setMessage] = useState(null);

  useEffect(() => {
    peopleService.getAll().then((people) => {
      setPeople(people);
    });
  }, []);

  const handleNameChange = (e) => {
    setNewName(e.target.value);
  };
  const handleNumberChange = (e) => {
    setNewNumber(e.target.value);
  };

  const ErrorNotification = ({ message }) => {
    if (message === null) {
      return null;
    }

    return <div className="error">{message}</div>;
  };

  const SuccessNotification = ({ message }) => {
    if (message === null) {
      return null;
    }

    return <div className="success">{message}</div>;
  };

  const addName = (e) => {
    e.preventDefault();
    const personObject = {
      name: newName,
      number: newNumber,
    };
    if (people.filter((n) => n.name === newName).length === 0) {
      peopleService
        .newPerson(personObject)
        .then((response) => {
          setPeople([...people, response]);
          setMessage(`Added ${newName}`);
          setTimeout(() => {
            setMessage(null);
          }, 5000);
        })
        .catch((error) => {
          //console.log(error.response.data.error);
          setErrorMessage(error.response.data.error);
          setTimeout(() => {
            setErrorMessage(null);
          }, 5000);

          setNewName(initialState);
          setNewNumber(initialState);
        });
    } else {
      let person = people.find((person) => person.name === newName);
      if (
        window.confirm(
          `${newName} is already in the phonebook. Do you want to replace the old number with a new one?`
        )
      ) {
        peopleService
          .modifyNumber(person.id, personObject)
          .then((response) => {
            setPeople(
              people.map((item) => (item.id !== person.id ? item : response))
            );
            setMessage(`Modified ${newName}`);
            setTimeout(() => {
              setMessage(null);
            }, 5000);
          })
          .catch((error) => {
            console.log(error.response.data);
            setErrorMessage(
              `the person ${person.name} was already deleted from server`
            );
            setTimeout(() => {
              setErrorMessage(null);
            }, 5000);
            setPeople(people.filter((n) => n.id !== person.id));
          });
      }
    }
    setNewName(initialState);
    setNewNumber(initialState);
  };

  const handleQuery = (e) => {
    setNewQuery(e.target.value);
  };

  const filteredpeople = people.filter((item) =>
    item.name.toLowerCase().includes(newQuery.toString())
  );

  const delPerson = (id, name) => {
    if (
      window.confirm(`Do you really want to delete ${name} from the phonebook?`)
    ) {
      peopleService
        .deletePerson(id)
        .then((response) => {
          setMessage(`the person ${name} was deleted from server`);
          setTimeout(() => {
            setMessage(null);
          }, 5000);
          setPeople(people.filter((n) => n.id !== id));
        })
        .catch((error) => {
          console.log(error);
          setErrorMessage(`the person ${name} was already deleted from server`);
          setTimeout(() => {
            setErrorMessage(null);
          }, 5000);
          setPeople(people.filter((n) => n.id !== id));
        });
    }
  };

  return (
    <div>
      <h2>Phonebook</h2>
      <ErrorNotification message={errorMessage} />
      <SuccessNotification message={message} />
      <FilterPerson handleQuery={handleQuery} newquery={newQuery} />
      <AddPerson
        addName={addName}
        newName={newName}
        newNumber={newNumber}
        handleNameChange={handleNameChange}
        handleNumberChange={handleNumberChange}
      />
      <h2>Numbers</h2>
      <ul>
        {filteredpeople.map((person, i) => (
          <Person key={i} person={person} delPerson={delPerson} />
        ))}
      </ul>
    </div>
  );
};

export default App;
